import {fromJS} from 'immutable'
import {SEARCH_FOCUS, SEARCH_BLUR, CHANGE_LIST, MOUSE_ENTER, MOUSE_LEAVE, LIST_CHANGE} from './actionTypes'

const defaultState = fromJS({
  focused: false,
  list: [],
  mouseIn: false,
  page: 1,
  totalPage: 1
})

export default (state = defaultState, action) => {
  switch (action.type) {
    case SEARCH_FOCUS:
      return state.set('focused', true)
    case SEARCH_BLUR:
      return state.set('focused', false)
    case CHANGE_LIST:
      return state.merge({
        list: action.value,
        totalPage: Math.ceil(action.value.size / 10)
      })
    case MOUSE_ENTER:
      return state.set('mouseIn', true)
    case MOUSE_LEAVE:
      return state.set('mouseIn', false)
    case LIST_CHANGE:
      const value = action.value > state.get('totalPage') ? 1 : action.value
      return state.set('page', value)
    default:
      return state
  }
}
